<template>
  <div class="content">
    <div class="panel">
      <p>Click on the links below to fetch data from the Flask server</p>
      <p>some stuff changed</p>
      <a href="" @click.prevent="fetchResource">Fetch Resource</a><br/>
      <h4>Results</h4>
      <p v-for="r in resources" :key="r.timestamp">
        Server Timestamp: {{r.timestamp | formatTimestamp }}
      </p>
      <p>{{error}}</p>
    </div>
  </div>
</template>

<script>

import $backend from '../backend'

export default {
  name: 'about',
  data () {
    return {
      resources: [],
      error: ''
    }
  },
  methods: {
    fetchResource () {
      $backend.fetchResource()
        .then(responseData => {
          this.resources.push(responseData)
        }).catch(error => {
          this.error = error.message
        })
    }
  }
}

</script>



